import baseUrl from '../../utils/utils.js'
export default {
	data() {
		return {
			newsLi:[],
			hours:0,
			minutes:0,
			seconds:0,
			nvabarData:"智能生态家居馆",
			bannerList:[],
			newsItem:[],
			title:'',
			title2:'',
			new:[],
			list:[],
			newBottom:[],
			new1:[],
			new2:[]
		}
	},

	methods: {
		more(){
			console.log(111);
			wx.switchTab({
				url:"/pages/news/news"
			})
		},
		coupon(){
			wx.navigateTo({
				url:"/pages/coupon/coupon"
			})
		},
		goDetail(e){
			console.log(111);
			wx.navigateTo({
				url:"../details/details?item="+JSON.stringify(this.newsItem[e.currentTarget.dataset.index])
			})
		}
		
	},
	onLoad(){
		var that=this
		uni.request({
			url:baseUrl+'index/index',
			method:'POST',
			success: (res) => {
				that.title=res.data.data.topicgoods[0].title 
				that.title2=res.data.data.topicgoods[1].title 
				for (var i = 0; i < res.data.data.banner.length; i++) {
						that.bannerList.push(res.data.data.banner[i].ad_code)
				}
				for (var i = 0; i < res.data.data.guessgoods.length; i++) {
						that.newsItem.push(res.data.data.guessgoods[i])
				}
				that.newsLi=res.data.data.topicgoods[0].goods_data
				that.new=res.data.data.topicgoods[1].goods_data
				for (var i = 0; i < that.newsLi.length; i++) {
						if(i>1){
							that.list.push(that.newsLi[i])
						}
				}
				for (var i = 0; i < that.new.length; i++) {
						if(i>1){
							that.newBottom.push(that.new[i])
						}
				}
				that.new1.push(that.newsLi[0])
				that.new2.push(that.newsLi[1])
				
				
			}
		})
	},
	onShow(){
		let _this=this
		var timer
       function time(){
            var date = new Date();
            // 获取当前时间距离----毫秒数
            var m1 = date.getTime();
            // 设置为当前天凌晨0:0:0
            date.setHours(0);
            date.setMinutes(0);
            date.setSeconds(0);
            // 获取当前天凌晨距离----毫秒数
            var m0 = date.getTime();
            // 获取距离明天的秒数
            var m = 60*60*24 - (m1-m0)/1000;
            if(m<=0){
                clearInterval(timer);
                return
            }
			var hour = parseInt(m/3600);
			var minute = parseInt((m%3600)/60)
			var second = parseInt((m%3600%60))
			if(hour<10){
				_this.hours="0"+hour
			}else{
				_this.hours=hour
			}
			if(minute<10){
				_this.minutes="0"+minute
			}else{
				_this.minutes=minute
			}
			if(second<10){
				_this.seconds="0"+second
			}else{
				_this.seconds=second
			}
        }
        setInterval(time,1000);
		
	}
}
